.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    // background-color: white;
    background: linear-gradient(0deg, #FFECD2 0%, #FCB69F 100%);
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: scroll;
    overflow-x: hidden;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    
    h2 {
        padding: 2rem;
    }
}

.library-song {

    display: flex;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    transition: all 0.5s ease;

    img {
        width: 30%;
    }

    &:hover {
        background-color: rgb( 222, 222, 255 );
    }
}

.song-description {
    padding-left: 1rem;

    h3 {
        font-size: 1rem;
    }

    h4 {
        font-size: 0.7rem;
    }
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155, 0.5) transparent;
}

*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba(155,155,155, 0.5);
    border-radius: 20px;
    border: transparent;
}

.selected {
    background-color: rgb( 222, 222, 255 );
}

.active-library {
    transform: translateX(0%);
    opacity: 1;
}