.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img {
        width: 20%;
        border-radius: 50%;
        transition: all 0.5s;
        transform: rotate(0deg);
    }

    h2 {
        padding: 3rem 1rem 1rem 1rem;
    }

    h3 {
        font-size: 1rem;
    }
}

.cover-active {
    animation: rotate 30s infinite linear forwards;
}

@keyframes rotate {
    
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@media screen and ( max-width: 768px ) {
    
    .song-container {

        img {
            width: 60%;
        }
    }
}