.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    // background-color: whitesmoke;
}

.time-control {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    input {
        width: 100%;
        padding: 1rem 0rem;
        cursor: pointer;
    }

    p {
        padding: 1rem;
    }
}

.play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 30%;

    svg {
        cursor: pointer;
    }
}

input[type=range] {
    width: 100%;
    margin: 3.45px 0;
    background-color: transparent;
    -webkit-appearance: none;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    // background: #202020;
    background: linear-gradient(90deg, #ffe4be 0%, #FCB69F 100%);
    // border: 0.2px solid #010101;
    border-radius: 50px;
    width: 100%;
    height: 9.1px;
    cursor: pointer;
  }
  input[type=range]::-webkit-slider-thumb {
    margin-top: -3.65px;
    width: 16px;
    height: 16px;
    background: #ffffff;
    border: 1px solid #000000;
    border-radius: 3px;
    cursor: pointer;
    -webkit-appearance: none;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #0d0d0d;
  }
  input[type=range]::-moz-range-track {
    background: rgba(0, 0, 0, 0);
    border: 0.2px solid #010101;
    border-radius: 1.3px;
    width: 100%;
    height: 9.1px;
    cursor: pointer;
  }
  input[type=range]::-moz-range-thumb {
    width: 16px;
    height: 16px;
    background: #ffffff;
    border: 1px solid #000000;
    border-radius: 3px;
    cursor: pointer;
  }
  


@media screen and ( max-width: 768px ) {
    
    .time-control {
        width: 90%;
    }

    .play-control {
        width: 80%;
    }
}