* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1, h2, h3 {
    color: rgb(47, 47, 47);
}

body {
    font-family: 'Lato', sans-serif;
}

h3, h4 {
    font-weight: 400;
    color: rgb(100, 100, 100);
}

.App {
    height: 100vh;
    transition: all 0.5s ease;
    background: #fff;
    
}

.library-active {
    margin-left: 10%;
}

@import "./player";
@import "./song";
@import "./library";
@import "./nav";