nav {
    min-height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;

    button {
        background-color: transparent;
        border: none;
        border: 2px solid rgb(65, 65, 65);
        cursor: pointer;
        padding: 0.5rem;
        transition: all 0.3s ease;

        &:hover {
            background-color: rgb(65, 65, 65);
            color: white;
        }
    }
}

@media screen and ( max-width: 768px ) {
    
    nav {

        button {
            z-index: 10;
        }
    }
}

@media screen and ( max-width: 768px ) {
    
    .library {
        width: 100%;
    }
}